const checkLogin = ({
  key,
  next,
  item = null,
  title = '',
  content = '',
  loginPath,
  cancel,
  confirmColor,
  cancelColor,
  confirmText,
  cancelText,
}) => {
  // 
  let user = uni.getStorageSync(key)
  if (user) {
    // 
    // 
    next(item)
  } else {
    uni.showModal({
      title,
      content,
      confirmColor,
      cancelColor,
      confirmText,
      cancelText,
      success: (res) => {
        if (res.confirm) {
          uni.navigateTo({
            url: loginPath
          })
        } else {
          cancel && cancel()
        }
      }
    })
  }
}

export default checkLogin