import api from "../../http/api.js"
import Vue from 'vue'
import dayjs from 'dayjs'

export default {
	// 
	namespaced: true,
	state: {
		//
		couponList:[],
		//
		orderList:[]
	},
	mutations: {
		setCouponList(state,data) {
			state.couponList=data
		},
		setOrderList(state,data) {
			state.orderList=data
		}
	},
	// 
	actions: {
		// async await
		// : 
		// action store 
		//
		async addOrders({
			commit
		}, item) {
			try {
				let res = await api.addOrder(item)
				if(res.code === 200) {
					uni.showToast({
						title: `${item.price}`,
						icon: 'none'
					})
					setTimeout(()=>{
						// 
						//
						uni.setStorageSync('toBePay',null)
						uni.setStorageSync('defaultAddress',null)
						uni.switchTab({
							url:'/pages/index/index'
						})
					},1000)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async getCoupons({
			commit
		},item) {
			try {
				let res = await api.getCoupon()
				if (res.code === 200) {
					res.data.map(item=>{
						Vue.set(item,'condition',`${item.threshold}`)
						Vue.set(item,'id',item._id)
						Vue.set(item,'reason','')
						Vue.set(item,'unitDesc','') 
						Vue.set(item,'available',1)
						Vue.set(item,'valueDesc',item.amount+'')
						Vue.set(item,'value',Number(item.amount)*100)
						Vue.set(item,'startAt',dayjs(item.start_time).valueOf())
						Vue.set(item,'endAt',dayjs(item.end_time).valueOf())
						Vue.set(item,'description',`${item.threshold}`)
					})
					commit('setCouponList',res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async getOrders({
			commit
		},item) {
			try {
				let res = await api.getAppOrder(item)
				if (res.code === 200) {
					commit('setOrderList', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async delOrders({
			commit,
			dispatch
		},item) {
			try {
				let res = await api.delOrder(item)
				if (res.code === 200) {
					uni.showToast({
						title:'',
						icon:'none'
					})
					dispatch('getOrders',item.user_id)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async addComment({commit},item) {
			try {
				let res = await api.createComment(item)
				if(res.code===200) {
					uni.showToast({
						title:'',
						icon:'none'
					})
					setTimeout(()=>{
						uni.navigateTo({
							url:'/pages/userorders/userorders'
						})
					},1000)
				}
			} catch (err) {
				console.log(err)
			}
		}
	}
}
