import api from "../../http/api.js"
import Vue from 'vue'
// import cloneDeep from 'lodash/cloneDeep'


export default {
	// 
	namespaced: true,
	state: {
		//
		hot: [],
		//
		searchListAll: [],
		//
		searchList: [],
		//
		searchTotal: 0,
		// 
		goods:null,
		// 
		recommendForYou:[],
		// 
		recommendPopular:[],
		//
		carList:[],
		//
		isDelete:false,
		//
		seckillList:[]
		
	},
	mutations: {
		setHot(state, data) {
			state.hot = data
		},
		setSearchAll(state, data) {
			state.searchListAll = data
		},
		setSearch(state, data) {
			state.searchList = data
		},
		setTotal(state, data) {
			state.searchTotal = data
		},
		setGoods(state, data) {
			state.goods = data
		},
		setRecommendForYou(state, data) {
			state.recommendForYou = data
		},
		setRecommendPopular(state, data) {
			state.recommendPopular = data
		},
		setCarList(state, data) {
			state.carList = data
		},
		setIsDelete(state,data) {
			state.isDelete=data
		},
		setSeckillList(state,data) {
			state.seckillList=data
		}
	},
	// 
	actions: {
		// async await
		// : 
		// action store 
		//
		async getHot({
			commit
		}) {
			try {
				let res = await api.getSearchWords()
				if (res.code === 200) {
					commit('setHot', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async getSearchAll({
			commit
		}, {
			query
		}) {
			try {
				let res = await api.search({
					current:1,
					pageSize:10000,
					query
				})
				if (res.code === 200) {
					commit('setSearchAll', res.data)
				}
			} catch (err) {
				wechatLogin(err)
			}
		},
		//
		async getSearch({
			commit
		}, {
			current,
			pageSize,
			query
		}) {
			try {
				let res = await api.search({
					current,
					pageSize,
					query
				})
				if (res.code === 200) {
					commit('setSearch', res.data)
					commit('setTotal', res.total)
				}
			} catch (err) {
				wechatLogin(err)
			}
		},
		//
		async getGoods({
			commit
		},id) {
			try {
				let res = await api.goodsDetail(id)
				if (res.code === 200) {
					commit('setGoods', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async getRecommendForYou({
			commit
		}) {
			try {
				let res = await api.getRecommend()
				if (res.code === 200) {
					commit('setRecommendForYou', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async getRecommendPopular({
			commit
		}) {
			try {
				let res = await api.getHot()
				if (res.code === 200) {
					commit('setRecommendPopular', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async addCart({
			commit
		},item) {
			try {
				let res = await api.addCart(item)
				if (res.code === 200) {
					uni.showToast({
						title:''
					})
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async getCart({
			commit
		},item) {
			try {
				let res = await api.getCart(item)
				if (res.code === 200) {
					res.data.map(item=>{
						Vue.set(item,'isChecked',false)
						Vue.set(item,'isDelete',false)
					})
					// commit('setCarList', cloneDeep(res.data))
					commit('setCarList', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async updateCart({
			commit
		},item) {
			try {
				let res = await api.updateCart(item)
			} catch (err) {
				console.log(err)
			}
		},
		//
		async delCart({
			commit
		},item) {
			try {
				let res = await api.delCart(item)
				commit('setIsDelete',res.code===200)
			} catch (err) {
				console.log(err)
			}
		},
		//
		//
		async getSeckill({
			commit
		}) {
			try {
				let res = await api.getSeckill()
				if (res.code === 200) {
					commit('setSeckillList', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
	}
}
