// ((browse) (search)...)
// key: 
// attr: 

const history = {
  saveHistory({
    key,
    data,
    attr
  }) {
    // 
    let name = key
    let history = uni.getStorageSync(name)
    if (history) {
      // 
      let arr = JSON.parse(uni.getStorageSync(name))
      // 
      let item = null
      if (typeof data === 'string') {
        item = arr.find(i => {
          return i[attr] === data
        })
      } else {
        item = arr.find(i => {
          return i[attr] === data[attr]
        })
      }
      // 
      if (!item) {
        if (typeof data === 'string') {
          let obj = {
            [attr]: data
          }
          arr.push(obj)
        } else {
          arr.push(data)
        }
        uni.setStorageSync(name, JSON.stringify(arr))
      }
    } else {
      // 
      // 
      let arr = []
      if (typeof data === 'string') {
        let obj = {
          name: data
        }
        arr.push(obj)
      } else {
        arr.push(data)
      }
      uni.setStorageSync(name, JSON.stringify(arr))
    }
  },
  // 
  getHistory({key}) {
    let name = key
    let arr = uni.getStorageSync(name)
    if (arr) return JSON.parse(arr)
    else return null
  }
}

export default history
