import api from "../../http/api.js"
export default {
	// 
	namespaced: true,
	state: {
		//
		send: false,
		//
		addressList: [],
	},
	mutations: {
		setSend(state, data) {
			state.send = data
		},
		setAddress(state, data) {
			state.addressList = data
		}
	},
	// 
	actions: {
		// async await
		// : 
		// action store 
		//
		async getCode({
			commit
		}, mobile) {
			try {
				let res = await api.getCode(mobile)
				commit('setSend', res.code === 200)
			} catch (err) {
				console.log(err)
			}
		},
		//
		async mobileLogin({
			commit
		}, item) {
			try {
				let res = await api.mobileLogin(item)
				uni.showToast({
					title: res.msg,
					icon: 'none'
				})
				if (res.code === 200) {
					//token 
					uni.setStorageSync('userInfo', res.data)
					uni.setStorageSync('token', res.token)
					setTimeout(() => {
						let isCar = uni.getStorageSync('isCar')
						if (isCar) {
							uni.navigateBack()
							uni.setStorageSync('isCar',null)
						} else {
							uni.switchTab({
								url: '/pages/index/index'
							})
						}
					}, 1000)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async userLogin({
			commit
		}, item) {
			try {
				let res = await api.userLogin(item)
				uni.showToast({
					title: res.msg,
					icon: 'none'
				})
				if (res.code === 200) {
					//token 
					uni.setStorageSync('userInfo', res.data)
					uni.setStorageSync('token', res.token)
					setTimeout(() => {
						let isCar = uni.getStorageSync('isCar')
						if (isCar) {
							uni.navigateBack()
							uni.setStorageSync('isCar',null)
						} else {
							uni.switchTab({
								url: '/pages/index/index'
							})
						}
					}, 1000)
				}
			} catch (err) {
				console.log(err)
			}
		},
		//
		async register({
			commit
		}, item) {
			try {
				let res = await api.register(item)
				if (res.code === 200) {
					uni.showToast({
						title: '',
						icon: 'none'
					})
					setTimeout(() => {
						uni.navigateTo({
							url: '/pages/login/login'
						})
					}, 1000)
				} else
					uni.showToast({
						title: ''
					})
			} catch (err) {
				console.log(err)
			}
		},
		// 
		async getAddress({
			commit
		}, item) {
			try {
				let res = await api.getAppAddress(item)
				if (res.code === 200) {
					commit('setAddress', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		// 
		async addAddress({
			commit
		}, item) {
			try {
				let res = await api.addAddress(item)
				if (res.code === 200) {
					uni.showToast({
						title:'',
						icon:'none'
					})
					setTimeout(()=>{
						uni.navigateTo({
							url:'/pages/address/address'
						})
					},1000)
				}
			} catch (err) {
				console.log(err)
			}
		},
		// 
		async delAddress({
			commit
		}, item) {
			try {
				let res = await api.delAddress(item)
				if (res.code === 200) {
					uni.showToast({
						title:'',
						icon:'none'
					})
					setTimeout(()=>{
						uni.navigateTo({
							url:'/pages/address/address'
						})
					},1000)
				}
			} catch (err) {
				console.log(err)
			}
		},
		// 
		async updateAddress({
			commit
		}, item) {
			try {
				let res = await api.updateAddress(item)
				if (res.code === 200) {
					uni.showToast({
						title:'',
						icon:'none'
					})
					setTimeout(()=>{
						uni.navigateTo({
							url:'/pages/address/address'
						})
					},1000)
				}
			} catch (err) {
				console.log(err)
			}
		}
	}
}
