import api from "../../http/api.js"
export default {
	// 
	namespaced: true,
	state: {
		//
		category: [],
	},
	mutations: {
		setCategory(state, data) {
			state.category = data
		}
	},
	// 
	actions: {
		// async await
		// : 
		// action store 
		//
		async getCategory({
			commit
		}) {
			try {
				let res = await api.getCategory()
				if (res.code === 200) {
					commit('setCategory', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		}
	}
}
