// export default http

import Fly from 'flyio/dist/npm/wx'






const fly = new Fly

fly.config.baseURL = 'http://localhost:7001'

fly.interceptors.request.use(config => {
	let token = uni.getStorageSync('token')
	if (token) {
		// headers
		config.headers['Authorization'] = token
	}
	return config
}, err => {
	return Promise.reject(err)
})

fly.interceptors.response.use(res => {
	return res.data
}, err => {
	//
	let status = err.status
	// 200 
	// 201 
	// 204 
	switch (status) {
		case 400:
			uni.showToast({
				title: '',
				icon: 'error'
			})
			break;
		case 401:
			uni.showToast({
				title: ',',
				icon: 'none'
			})
			setTimeout(()=>{
				uni.navigateTo({
					url:'/pages/login/login'
				})
			},1000)
			break
		case 403:
			uni.showToast({
				title: '',
				icon: 'error'
			})
			break
		case 404:
			uni.showToast({
				title: '',
				icon: 'error'
			})
			break
		case 422:
			uni.showToast({
				title: '',
				icon: 'error'
			})
			break
		case 500:
			uni.showToast({
				title: '',
				icon: 'error'
			})
			break;
	}
	console.log(err)
	return Promise.reject(err)
})

export default fly
